<template>
    <div class="unbund flexCol">
        <!--  -->
        <errAlert :errShow="errShow" :errTxt="errTxt"></errAlert>
        <loading v-if="loading"></loading>
        <smallUser></smallUser>
        <!--  -->
        <h1 class="validate" v-if="validate">{{validate}}</h1>
        <!--  -->
        <div class="unbund-model borderBox flexRow">
            <span></span>
            <p>{{userMsg.username}}</p>
        </div>
        <!--  -->
        <div class="unbund-model borderBox flexRow">
            <span></span>
            <p>{{userMsg.card}}</p>
        </div>
        <!--  -->
        <div class="unbund-model borderBox flexRow">
            <span></span>
            <p>{{userMsg.phone}}</p>
        </div>
        <!--  -->
        <div class="unbund-model borderBox flexRow noBorder">
            <input type="text" maxlength="6" v-model="postMsg.verification" placeholder="6"></input>
            <!--  -->
            <button type="button" @click="getCode()" :disabled="!showVer" class="verificationBtn borderBox">
                <span v-show="showVer"></span>
                <span v-show="!showVer" class="count">({{count}}s)</span>
            </button>
        </div>
        <button type="button" @click="unbund" class="btn"></button>
    </div>
</template>
<script type="text/javascript">
export default {
    name: 'unbundIdcard',
    data() {
        return {
            loading: false,
            // 
            userMsg: {},
            // 
            validate: '',
            // 
            showVer: true,
            count: '',
            timer: null,
            TIME_COUNT: 60,
            // 
            postMsg: {
                card: '',
                verification: '',
                phone: ''
            },
            // 
            errShow: false,
            errTxt: '',
        }
    },
    activated() {
        document.title = "";
        this.userMsg = this.$route.params.userMsg;
        this.validate = '';
    },
    methods: {
        // 
        getCode(phone) {
            var that = this;
            this.postMsg.phone = this.userMsg.phones;
            // 
            if (!this.timer) {
                // 
                this.count = this.TIME_COUNT;
                this.showVer = false;
                // 
                this.timer = setInterval(() => {
                        if (this.count > 1 && this.count <= this.TIME_COUNT) {
                            this.count--;
                        } else {
                            this.showVer = true;
                            clearInterval(this.timer);
                            this.timer = null;
                        }
                    }, 1000)
                    // 
                this.$tools.PostDataToServer(
                    this,
                    this.API.getCode,
                    this.postMsg,
                    function success(res) {
                        console.log(res);
                    },
                    function error(err) {
                        that.errShow = true;
                        that.errTxt = ''
                        var errLoading = setTimeout(function() {
                            that.errShow = false;
                            that.errTxt = ''
                            clearTimeout(errLoading);
                        }, 2000)
                    }
                )
            }
        },
        // 
        unbund() {
            var verification = this.postMsg.verification,
                verificationRe = /^\d{6}$/,
                that = this;
            // 
            this.postMsg.card = this.userMsg.cards;
            this.postMsg.phone = this.userMsg.phones;
            if (!verificationRe.test(verification)) {
                this.validate = ""
            } else {
                this.validate = '';
                this.loading = true;
                // 
                this.$tools.PostDataToServer(
                    this,
                    this.API.unbund,
                    this.postMsg,
                    function success(res) {
                        that.loading = false;
                        var resData = res.data;
                        // 
                        if (resData.State.Code == 1) {
                            window.location.href = process.env.API_HOST;
                        }
                        // 
                        if (resData.State.Code == 0) {}
                    },
                    // 
                    function error(err) {
                        that.loading = false;
                        that.errShow = true;
                        that.errTxt = ''
                        var errLoading = setTimeout(function() {
                            that.errShow = false;
                            that.errTxt = ''
                            clearTimeout(errLoading);
                        }, 2000)
                    }
                )
            }
        }
    }
}
</script>
<style type="text/css" scoped>
.unbund {
    -ms-align-items: center;
    align-items: center;
}


/**/

.unbund-model {
    width: 100%;
    padding: 0 5%;
    height: 4rem;
    font-size: .875rem;
    background: #fff;
    border-bottom: 1px solid #ddd;
    margin: 0 auto;
    justify-content: space-between;
    -ms-align-items: center;
    align-items: center;
}

.unbund-model>span {
    color: #5f5f5f;
    background: #EFEFEF;
    padding: .2rem .75rem;
    border-radius: .2rem;
    margin-right: 1rem;
}

.unbund-model input {
    outline: none;
    border-radius: .2rem;
    border: 1px solid #ddd;
    height: 2rem;
}


/**/

.unbund .verificationBtn {
    min-width: 8rem;
    padding: .5rem;
    font-size: .875rem;
    outline: none;
    border: none;
    border-radius: .2rem;
    background: #7DC5DC;
    text-align: center;
    color: #fff;
}

.btn {
    width: 90%;
}
</style>



