<template>
    <div class="uploadImg">
        <div class="show">
            <div class="picture" :style="'backgroundImage:url('+baseImage+')'"></div>
        </div>
        <div class="uploadFile">
            <div class="uploadFile_style">
                <slot v-if="!baseImage"></slot>
            </div>
            <!-- <img src="../assets/common/del.png" v-if="baseImage" class="delImg" @click = "delImg"> -->
            <input type="file" id="upload" accept="image" @change="upload">
        </div>
    </div>
</template>
<script>
import Exif from 'exif-js'
export default {
    data() {
            return {
                baseImage: '',
                picValue: ''
            }
        },
    // 
    deactivated() {
        this.picValue = '';
        this.baseImage = '';
    },
    methods: {
        // 
        // delImg () {
        //   this.picValue = '';
        //   this.baseImage = '';
        // },
        // 
        upload(e) {
            let files = e.target.files || e.dataTransfer.files;
            if (!files.length) return;
            this.picValue = files[0];
            this.imgPreview(this.picValue);
        },
        imgPreview(file) {
            let self = this;
            let Orientation;
            //
            Exif.getData(file, function() {
                Orientation = Exif.getTag(this, 'Orientation');
            });
            // FileReader
            if (!file || !window.FileReader) return;

            if (/^image/.test(file.type)) {
                // reader
                let reader = new FileReader();
                //  base64 
                reader.readAsDataURL(file);
                // 
                reader.onloadend = function() {
                    let result = this.result;
                    let img = new Image();
                    img.src = result;
                    //100K,
                    if (this.result.length <= (100 * 1024)) {
                        self.baseImage = this.result;
                        // base64
                        self.$emit('popBaseImg', self.baseImage);
                        self.postImg();
                    } else {
                        img.onload = function() {
                            let data = self.compress(img, Orientation);
                            self.baseImage = data;
                            // base64
                            self.$emit('popBaseImg', self.baseImage);
                            self.postImg();
                        }
                    }
                }
            }
        },
        //
        postImg() {},
        //
        // 
        rotateImg(img, direction, canvas) {
            //4
            const min_step = 0;
            const max_step = 3;
            if (img == null) return;
            //imgimg
            let height = img.height;
            let width = img.width;
            let step = 2;
            if (step == null) {
                step = min_step;
            }
            if (direction == 'right') {
                step++;
                //
                step > max_step && (step = min_step);
            } else {
                step--;
                step < min_step && (step = max_step);
            }
            //
            let degree = step * 90 * Math.PI / 180;
            let ctx = canvas.getContext('2d');
            switch (step) {
                case 0:
                    canvas.width = width;
                    canvas.height = height;
                    ctx.drawImage(img, 0, 0);
                    break;
                case 1:
                    canvas.width = height;
                    canvas.height = width;
                    ctx.rotate(degree);
                    ctx.drawImage(img, 0, -height);
                    break;
                case 2:
                    canvas.width = width;
                    canvas.height = height;
                    ctx.rotate(degree);
                    ctx.drawImage(img, -width, -height);
                    break;
                case 3:
                    canvas.width = height;
                    canvas.height = width;
                    ctx.rotate(degree);
                    ctx.drawImage(img, -width, 0);
                    break;
            }
        },
        // 
        compress(img, Orientation) {
            let canvas = document.createElement("canvas");
            let ctx = canvas.getContext('2d');
            //canvas
            let tCanvas = document.createElement("canvas");
            let tctx = tCanvas.getContext("2d");
            let initSize = img.src.length;
            let width = img.width;
            let height = img.height;
            //400
            let ratio;
            if ((ratio = width * height / 4000000) > 1) {
                console.log("400")
                ratio = Math.sqrt(ratio);
                width /= ratio;
                height /= ratio;
            } else {
                ratio = 1;
            }
            canvas.width = width;
            canvas.height = height;
            //        
            ctx.fillStyle = "#fff";
            ctx.fillRect(0, 0, canvas.width, canvas.height);
            //100
            let count;
            if ((count = width * height / 1000000) > 1) {
                console.log("100W");
                count = ~~(Math.sqrt(count) + 1); //
                //            
                let nw = ~~(width / count);
                let nh = ~~(height / count);
                tCanvas.width = nw;
                tCanvas.height = nh;
                for (let i = 0; i < count; i++) {
                    for (let j = 0; j < count; j++) {
                        tctx.drawImage(img, i * nw * ratio, j * nh * ratio, nw * ratio, nh * ratio, 0, 0, nw, nh);
                        ctx.drawImage(tCanvas, i * nw, j * nh, nw, nh);
                    }
                }
            } else {
                ctx.drawImage(img, 0, 0, width, height);
            }
            //ios 
            if (Orientation != "" && Orientation != 1) {
                switch (Orientation) {
                    case 6: //90
                        this.rotateImg(img, 'left', canvas);
                        break;
                    case 8: //90
                        this.rotateImg(img, 'right', canvas);
                        break;
                    case 3: //180
                        this.rotateImg(img, 'right', canvas); //
                        this.rotateImg(img, 'right', canvas);
                        break;
                }
            }
            //
            let ndata = canvas.toDataURL('image/jpeg', 0.1);
            console.log('' + initSize);
            console.log('' + ndata.length);
            console.log('' + ~~(100 * (initSize - ndata.length) / initSize) + "%");
            tCanvas.width = tCanvas.height = canvas.width = canvas.height = 0;
            return ndata;
        },
    }
}
</script>
<style scoped>
.uploadImg {
    width: 16rem;
    height: 8rem;
    margin: 0 auto 1rem;
    position: relative;
    z-index: 1;
}


/**/

.show {
    width: 100%;
    height: 100%;
    overflow: hidden;
    border-radius: .5rem;
    background: #ABDDF1;
    position: relative;
}


/**/

.picture {
    width: 100%;
    height: 100%;
    overflow: hidden;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
}


/**/

.uploadFile {
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    background: none;
}

.uploadFile input {
    width: 100%;
    height: 100%;
    outline: none;
    opacity: 0;
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
}


/**/

.uploadFile_style {
    width: 100%;
    height: 100%;
    font-size: .875rem;
    color: #005787;
    /*flex*/
    display: box;
    display: -webkit-box;
    display: -moz-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -ms-align-items: center;
    align-items: center;
    -webkit-flex-direction: column;
    -moz-flex-direction: column;
    -ms-flex-direction: column;
    -o-flex-direction: column;
    flex-direction: column;
    justify-content: center;
    -ms-align-items: center;
    align-items: center;
}

.uploadFile_style img {
    margin: .5rem auto;
    height: 2rem;
}


/**/

.delImg {
    position: absolute;
    z-index: 111;
    top: 1rem;
    right: 1rem;
    width: 1.5rem;
    height: 1.5rem;
}
</style>



