<template>
    <div class="OtherUserAdd flexCol">
        <!--  -->
        <errAlert :errShow="errShow" :errTxt="errTxt"></errAlert>
        <loading v-if="loading"></loading>
        <!--  -->
        <h1 class="validate" v-if="validate">{{validate}}</h1>
        <!--  -->
        <div class="small-header"></div>
        <!--  -->
        <form class="pay-other-msg flexCol">
            <div class="pay-other-model flexRow">
                <span class="userMsg-title"></span>
                <span class="userMsg-kind">
                  <input type="text" class="pay-other-form  pay-form-name" placeholder="" v-model="OtherUser.username"></input>
                </span>
            </div>
            <div class="pay-other-model flexRow">
                <span class="userMsg-title"></span>
                <span class="userMsg-kind">
                  <select class="pay-other-form" name="relative" v-model="OtherUser.relationship">
                      <option disabled value=""></option>
                      <option value=""></option>
                      <option value=""></option>
                      <option value=""></option>
                      <option value=""></option>
                      <option value=""></option>
                    </select>
                </span>
            </div>
            <div class="pay-other-model flexRow">
                <span class="userMsg-title"></span>
                <span class="userMsg-kind">
                  <input type="tel" class="pay-other-form" placeholder="18" minlength="18" maxlength="18" v-model="OtherUser.card"></input>
                </span>
            </div>
            <div class="pay-other-model flexRow">
                <span class="userMsg-title"></span>
                <span class="userMsg-kind">
                    <input type="tel" class="pay-other-form" placeholder="11" minlength="11" maxlength="11" v-model="OtherUser.phone"></input>
                </span>
            </div>
            <!--  -->
            <button class="btn" type="button" @click='showPayComfirm'></button>
        </form>
        <!-- error -->
        <div class="pay-other-errorWin flexRow" v-if="showError">
            <img src="../../assets/common/noData.png" alt="">
            <div>
                <p>{{errTxt}}</p>
            </div>
        </div>
    </div>
</template>
<script type="text/javascript">
export default {
    name: 'OtherUserAdd',
    data() {
        return {
            loading: false,
            // 
            OtherUser: {
                username: '',
                relationship: "",
                card: '',
                phone: '',
                status: 2
            },
            // 
            errShow: false,
            errTxt: '',
            validate: ''
        }
    },
    activated() {
        document.title = "";
        this.validate = '';
        // 
        this.OtherUser.username = '';
        this.OtherUser.card = '';
        this.OtherUser.phone = '';
        this.OtherUser.relationship = "";
    },
    methods: {
        // 
        showPayComfirm() {
            // 
            var nameRe = /[\u4E00-\u9FA5]{2,5}(?:[\u4E00-\u9FA5]{2,5})*/,
                idCardRe = /^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$/,
                phoneRe = /^[1][3,4,5,7,8,9][0-9]{9}$/;
            // 
            var name = this.OtherUser.username,
                idCard = this.OtherUser.card,
                phone = this.OtherUser.phone;
            var that = this;
            // 
            if (!nameRe.test(name)) {
                this.validate = ""
            } else {
                if (this.relationship == "") {
                    this.validate = ""
                } else {
                    if (!idCardRe.test(idCard)) {
                        this.validate = ""
                    } else {
                        if (!phoneRe.test(phone)) {
                            this.validate = ""
                        } else {
                            // 
                            if (nameRe.test(name) && idCardRe.test(idCard) && phoneRe.test(phone)) {
                                this.validate = '';
                                this.loading = true;
                                // 
                                this.$tools.PostDataToServer(
                                    this,
                                    this.API.addOther,
                                    this.OtherUser,
                                    function success(res) {
                                        var resData = res.data;
                                        that.loading = false;
                                        if (resData.State.Code == 1) {
                                            // 
                                            that.$router.replace('/pay-other');
                                        }
                                        // 
                                        if (resData.State.Code == 0) {
                                            // 
                                            that.OtherUser.username = '';
                                            that.OtherUser.card = '';
                                            that.OtherUser.phone = '';
                                            that.OtherUser.relationship = "";
                                            // 
                                            that.errShow = true;
                                            that.errTxt = resData.Message;
                                            var errLoading = setTimeout(function() {
                                                that.errShow = false;
                                                that.errTxt = ''
                                                clearTimeout(errLoading);
                                            }, 2000);
                                        }
                                    },
                                    // 
                                    function error(err) {
                                        that.loading = false;
                                        that.errShow = true;
                                        that.errTxt = ''
                                        var errLoading = setTimeout(function() {
                                            that.errShow = false;
                                            that.errTxt = ''
                                            clearTimeout(errLoading);
                                        }, 2000);
                                    }
                                )
                            }
                        }
                    }
                }
            }
        },
    }
}
</script>
<style type="text/css" scoped>
.OtherUserAdd {
    font-size: .875rem;
    justify-content: center;
    -ms-align-items: center;
    align-items: center;
}


/**/

.pay-other-msg {
    border: 1px solid #ddd;
    font-size: .875rem;
    margin: 1rem auto .5rem;
    width: 80%;
    padding: 0 5%;
    border-radius: .5rem;
}

.pay-other-model {
    width: 100%;
    height: 2rem;
    -ms-align-items: center;
    align-items: center;
    padding: .25rem 0;
    border-bottom: 1px dashed #ddd;
}

.pay-other-model:last-child {
    border: none;
    margin-bottom: 0;
}

.pay-other-model .noBorder {
    border-bottom: none;
}


/**/

.userMsg-title {
    color: #31679C;
    font-weight: bold;
    width: 6rem;
}


/**/

.userMsg-kind {
    color: #5F5F5F;
    background: #EFEFEF;
    padding: 2px .5rem;
    border-radius: .2rem;
    font-weight: normal;
    -webkit-flex: 1;
    -moz-flex: 1;
    -ms-flex: 1;
    -o-flex: 1;
    flex: 1;
}


/**/

.pay-other-form {
    border: none;
    background: #EFEFEF;
    color: #5F5F5F;
    padding: 2px .5rem;
    border-radius: 1rem;
    font-weight: normal;
    outline: none;
}

.btn {
    width: 100%;
}


/**/

.pay-other-errorWin {
    position: fixed;
    width: 76%;
    left: 50%;
    top: 30%;
    margin-left: -40%;
    border: 1px solid #ddd;
    border-radius: .3rem;
    background: #fff;
    padding: 2%;
    justify-content: space-around;
    -ms-align-items: center;
    align-items: center;
    z-index: 10;
}

.pay-other-errorWin img {
    display: inline-block;
    width: 3rem;
    height: 3rem;
}
</style>



