<template>
    <div class="pay-my flexCol borderBox">
        <!--  -->
        <errAlert :errShow="errShow" :errTxt="errTxt"></errAlert>
        <loading v-if="loading"></loading>
        <smallUser></smallUser>
        <alert :alert="alert" :title="title" :text="text"></alert>
        <!--  -->
        <div class="pay-my-true flexCol borderBox" v-if="flag">
            <div class="pay-my-model">
                <span class="userMsg-name">{{payMsg.username}}</span>
                <span><span class="userMsg-kind">{{payMsg.insurance_type1}}</span></span>
            </div>
            <div class="pay-my-model">
                <span><span class="model-text">{{payMsg.payment_amount}}</span></span>
            </div>
            <div class="pay-my-model">
                <span>{{payMsg.valid_period}}</span>
            </div>
            <button class="btn" @click="payMy"></button>
        </div>
        <!--  -->
        <div class="pay-my-true" v-else>
            <div class="pay-my-model">
                <span class="userMsg-name">{{payMsg.username}}</span>
                <span><span class="userMsg-kind">{{payMsg.insurance_type1}}</span></span>
            </div>
            <div class="pay-my-model">
                <span></span>
            </div>
            <div class="pay-my-model">
                <span>{{payMsg.valid_period}}</span>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: 'pay-my',
    data() {
        return {
            // alert
            alert: false,
            title: '',
            text: '',
            // 
            loading: true,
            flag: true,
            // 
            payMsg: {},
            // 
            errShow: false,
            errTxt: '',
            // axios
            postData: {
                status: 1
            }
        }
    },
    activated() {
        document.title = "";
        // 
        this.getPayMsg();
    },
    methods: {
        // 
        payMy() {
            var that = this;
            // 
            this.$tools.GetDataFromServer(
                this,
                this.API.payMay,
                function success(res) {
                    var resData = res.data;
                    that.weixinPay(resData);
                },
                function error(err) {
                    that.errShow = true;
                    that.errTxt = ''
                    var errLoading = setTimeout(function() {
                        that.errShow = false;
                        that.errTxt = ''
                        clearTimeout(errLoading);
                    }, 2000)
                }
            );
        },
        /**
         * @method :
         * @param data
         */
        weixinPay(data) {
            var that = this;
            if (typeof WeixinJSBridge == "undefined") { //
                if (document.addEventListener) {
                    document.addEventListener('WeixinJSBridgeReady', that.onBridgeReady(data), false);
                } else if (document.attachEvent) {
                    document.attachEvent('WeixinJSBridgeReady', that.onBridgeReady(data));
                    document.attachEvent('onWeixinJSBridgeReady', that.onBridgeReady(data));
                }
            } else {
                that.onBridgeReady(data);
            }
        },
        /**
         * @method 
         * @param data:,(APIH5);
         */
        onBridgeReady(data) {
            var that = this;
            WeixinJSBridge.invoke(
                'getBrandWCPayRequest', {
                    "appId": data.appId, //
                    "timeStamp": data.timeStamp, //1970
                    "nonceStr": data.nonceStr, //
                    "package": data.package,
                    "signType": data.signType, //
                    "paySign": data.paySign //
                },
                function(res) {
                    that.loading = true;
                    // ,res.err_msgok
                    if (res.err_msg == "get_brand_wcpay_request:ok") {
                        that.getPayMsg();
                    } else {
                        that.loading = false;
                        that.alert = true;
                        that.title = "";
                        that.text = "";
                    }
                }
            );
        },
        // 
        getPayMsg() {
            var that = this;
            // 
            this.$tools.PostDataToServer(
                this,
                this.API.getMyPay,
                this.postData,
                function success(res) {
                    var resData = res.data;
                    that.loading = false;
                    if (resData.State.Code == 1) {
                        that.payMsg = resData.Info;
                        that.flag = true;
                    }else{
                        that.payMsg = resData.Info;
                        that.flag = false;
                    }
                },
                function error(err) {
                    that.loading = false;
                    that.errShow = true;
                    that.errTxt = ''
                    var errLoading = setTimeout(function() {
                        that.errShow = false;
                        that.errTxt = ''
                        clearTimeout(errLoading);
                    }, 2000);
                }
            )
        },
        testBtn() {
            this.flag = false;
        }
    }
}
</script>
<style scoped>
.pay-my,
.pay-my-true {
    font-size: .875rem;
    width: 100%;
    -webkit-flex: 1;
    -moz-flex: 1;
    -ms-flex: 1;
    -o-flex: 1;
    flex: 1;
}

.pay-my-true {
    width: 90%;
    padding: 0 5%;
    border: 1px solid #ddd;
    font-size: .875rem;
    margin: 1rem auto;
    border-radius: .5rem;
}

.pay-my-model {
    padding: .5rem 0;
    border-bottom: 1px dashed #ddd;
}

.pay-my-model:last-child {
    margin-bottom: 0;
    border-bottom: none;
}

.userMsg-name {
    font-weight: bold;
    font-size: .875rem;
    margin-right: 1rem;
}

.userMsg-kind {
    color: #5F5F5F;
    background: #EFEFEF;
    padding: 2px .5rem;
    margin: .5rem;
    border-radius: 1rem;
}

.btn {
    width: 100%;
}
</style>



