<template>
    <div class="notion flexRow">
        <!--  -->
        <errAlert :errShow="errShow" :errTxt="errTxt"></errAlert>
        <loading v-if="loading"></loading>
        <smallUser></smallUser>
        <!--  -->
        <h1 class="validate" v-if="validate">{{validate}}</h1>
        <div class="notion-item borderBox flexRow">
            <span></span>
            <!--  -->
            <select name="notion" class="info" v-model="notion.notion" id="notion">
                <option v-for="item in notionList" :value="item.name">{{item.name}}</option>
            </select>
        </div>
        <!--  -->
        <button class="btn" @click="postAdd"></button>
    </div>
</template>
<script type="text/javascript">
export default {
    name: 'notion',
    data() {
        return {
            loading: false,
            // 
            notion: {
                notion: ''
            },
            notionList: [{"name":""},{"name":""},{"name":""},{"name":""},{"name":""},{"name":""},{"name":""},{"name":""},{"name":""},{"name":""},{"name":""},{"name":""},{"name":""},{"name":""},{"name":""},{"name":""},{"name":""},{"name":""},{"name":""},{"name":""},{"name":""},{"name":""},{"name":""},{"name":""},{"name":""},{"name":""},{"name":""},{"name":""},{"name":""},{"name":""},{"name":""},{"name":""},{"name":""},{"name":""},{"name":""},{"name":""},{"name":""},{"name":""},{"name":""},{"name":""},{"name":""},{"name":""},{"name":""},{"name":""},{"name":""},{"name":""},{"name":""},{"name":""},{"name":""},{"name":""},{"name":""},{"name":""},{"name":""},{"name":""},{"name":""},{"name":""},{"name":""}],
            // 
            validate: '',
            // 
            notionSuccess: false,
            // 
            errShow: false,
            errTxt: ''
        }
    },
    activated() {
        document.title = "";
    },
    methods: {
        // 
        checkNotion() {
            if (this.notion.notion == '' || this.notion.notion == '') {
                this.validate = ''
            } else {
                this.notionSuccess = true;
            }
        },
        // &
        postAdd() {
            this.checkNotion();
            // 
            if (this.notionSuccess) {
                this.validate = '';
                this.loading = true;
                var that = this;
                // 
                this.$tools.PostDataToServer(
                    this,
                    this.API.notion,
                    this.notion,
                    function success(res) {
                        that.loading = false;
                        var resData = res.data;
                        // 
                        if (resData.State.Code == 1) {
                            that.$router.replace('/user');
                        }
                    },
                    // 
                    function error(err) {
                        that.loading = false;
                        that.errShow = true;
                        that.errTxt = ''
                        var errLoading = setTimeout(function() {
                            that.errShow = false;
                            that.errTxt = ''
                            clearTimeout(errLoading);
                        }, 2000)
                    }
                )
            }
        }
    }
}
</script>
<style type="text/css" scoped>
.notion {
    flex-direction: column;
    -ms-align-items: center;
    align-items: center;
}


/**/

.notion-item {
    width: 100%;
    height: 3rem;
    padding: 0 5%;
    border-bottom: 1px solid #eee;
    font-size: .875rem;
    justify-content: space-between;
    -ms-align-items: center;
    align-items: center;
}

.notion-item span {
    width: 4rem;
}

.notion-item select {
    height: 2rem;
    border-radius: .2rem;
    border: 1px solid #ddd;
    outline: none;
    color: #606060;
    -webkit-flex: 1;
    -moz-flex: 1;
    -ms-flex: 1;
    -o-flex: 1;
    flex: 1;
}

.btn {
    width: 65%;
}
</style>



