<template>
    <div class="appealPhone flexCol">
        <!--  -->
        <errAlert :errShow="errShow" :errTxt="errTxt"></errAlert>
        <loading v-if="loading"></loading>
        <div class="small-header"></div>
        <alert :alert="alert" :title="title" :text="text"></alert>
        <!--  -->
        <h1 class="validate" v-if="validate">{{validate}}</h1>
        <!--  slot -->
        <uploadImg @popBaseImg="popBaseImg_1">
            <img src="../../assets/register/idcard_1.png">
            <p>()</p>
            <img src="../../assets/common/add.png">
        </uploadImg>
        <uploadImg @popBaseImg="popBaseImg_2">
            <img src="../../assets/register/idcard_2.png">
            <p>()</p>
            <img src="../../assets/common/add.png">
        </uploadImg>
        <button @click="upload" class="btn"></button>
    </div>
</template>
<script type="text/javascript">
import uploadImg from '@/components/uploadImg'
export default {
    name: 'appealPhone',
    data() {
        return {
            loading: false,
            img: {
                baseImage_front: '',
                baseImage_back: ''
            },
            // 
            passWin: false,
            // 
            errShow: false,
            errTxt: '',
            validate: '',
            // 
            alert: false,
            title: '',
            text: ''
        }
    },
    // 
    activated() {
        document.title = "";
        this.passWin = false;
        this.img.baseImage_front = '';
        this.img.baseImage_back = '';
        this.validate = '';
    },
    methods: {
        // 
        popBaseImg_1(baseImg) {
            this.img.baseImage_front = baseImg;
        },
        popBaseImg_2(baseImg) {
            this.img.baseImage_back = baseImg;
        },
        // 
        upload() {
            if (this.img.baseImage_front == undefined || this.img.baseImage_back == undefined) {
                this.validate = ''
            } else {
                this.validate = '';
                this.loading = true;
                var that = this;
                this.$tools.PostDataToServer(
                    this,
                    this.API.postImg,
                    this.img,
                    function success(res) {
                        that.loading = false;
                        var resData = res.data;
                        // 1.5s
                        if (resData.State.Code == 1) {
                            that.alert = true;
                            that.title = "";
                            that.text = "<p>,</p>" + "<p>,</p>" + "<p></p>";
                            // 
                            var pass = setTimeout(function() {
                                that.$router.replace('/');
                                clearTimeout(pass);
                            }, 1500);
                        }
                    },
                    function error(err) {
                        that.loading = false;
                        that.errShow = true;
                        that.errTxt = ''
                        var errLoading = setTimeout(function() {
                            that.errShow = false;
                            that.errTxt = ''
                            clearTimeout(errLoading);
                        }, 2000);
                    }
                )
            }
        }
    },
    components: {
        uploadImg
    }
}
</script>
<style type="text/css" scoped>
.small-header {
    margin-bottom: 1rem;
}

.appealPhone {
    width: 100%;
    -ms-align-items: center;
    align-items: center;
}

.btn {
    width: 45%;
}
</style>



