<template>
    <div class="search-my">
        <!--  -->
        <errAlert :errShow="errShow" :errTxt="errTxt"></errAlert>
        <loading v-if="loading"></loading>
        <smallUser></smallUser>
        <!--  -->
        <div class="search-my-title flexRow">
            <span></span>
        </div>
        <!-- userType1userType2 -->
        <!--  -->
        <div class="search-my-msg flexCol">
            <div class="search-model flexRow">
                <div>
                    <span class="search-model-title"></span>
                    <span class="search-model-msg">{{search.username}}</span>
                </div>
                <div>
                    <span class="search-model-title"></span>
                    <span class="search-model-msg" v-if="search.sex == '1'"></span>
                    <span class="search-model-msg" v-else-if="search.sex == '2'"></span>
                </div>
            </div>
            <div class="search-model flexRow">
                <span class="search-model-title"></span>
                <span class="search-model-msg">{{search.card}}</span>
            </div>
            <div class="search-model flexRow">
                <span class="search-model-title"></span>
                <span class="search-model-msg">{{search.phone}}</span>
            </div>
            <!--  -->
            <div class="search-model flexRow" v-if="userType == 2">
                <span class="search-model-title"></span>
                <span class="search-model-msg">{{search.currentdeadline}}</span>
            </div>
            <!--  -->
            <!--  -->
            <div class="search-model flexRow" v-if="userType == 1">
                <span class="search-model-title"></span>
                <span class="search-model-msg">{{search.balance}}</span>
            </div>
            <div class="search-model flexRow" v-if="userType == 1">
                <span class="search-model-title"></span>
                <span class="search-model-msg">{{search.oldyearincome}}</span>
            </div>
            <div class="search-model flexRow" v-if="userType == 1">
                <span class="search-model-title"></span>
                <span class="search-model-msg">{{search.nowyearincome}}</span>
            </div>
            <div class="search-model flexRow" v-if="userType == 1">
                <span class="search-model-title"></span>
                <span class="search-model-msg">{{search.nowexpenditure}}</span>
            </div>
            <div class="search-model flexRow" v-if="userType == 1">
                <span class="search-model-title"></span>
                <span class="search-model-msg">{{search.insurance_info}}</span>
            </div>
            <div class="search-model flexRow" v-if="userType == 1">
                <span class="search-model-title"></span>
                <span class="search-model-msg">{{search.insured_name}}</span>
            </div>
            <!--  -->
            <div class="search-model flexRow">
                <span class="search-model-title"></span>
                <span class="search-model-msg">{{search.address}}</span>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: 'search-my',
    data() {
        return {
            search: {},
            errShow: false,
            errTxt: '',
            userType: '',
            loading: true
        }
    },
    activated() {
        document.title = "";
        // 
        var userType = this.$store.state.userType;
        if (userType == '') {
            this.userType = 1;
        }
        if (userType == '' || userType == '') {
            this.userType = 2;
        }
        // 
        var that = this;
        this.$tools.GetDataFromServer(
            this,
            this.API.searchMy,
            function success(res) {
                var resData = res.data;
                that.loading = false;
                if (resData.State.Code == 1) {
                    that.search = resData.Info;
                }
                if (resData.State.Code != 1) {
                    that.errShow = true;
                    that.errTxt = ''
                    var errLoading = setTimeout(function() {
                        that.errShow = false;
                        that.errTxt = ''
                        clearTimeout(errLoading);
                    }, 2000)
                }
            },
            function error(err) {
                that.loading = false;
                that.errShow = true;
                that.errTxt = ''
                var errLoading = setTimeout(function() {
                    that.errShow = false;
                    that.errTxt = ''
                    clearTimeout(errLoading);
                }, 2000)
            }
        );
    }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
/**/

.search-my {
    background: #fff;
    font-size: .875rem;
}


/* */

.search-my-title {
    color: #015480;
    font-weight: bold;
    width: 90%;
    height: 3rem;
    margin: 0 auto;
    padding: 0 2.5%;
    border-radius: .4rem .4rem 0 0;
    border-bottom: 1px solid #015480;
    background: #fff;
    -ms-align-items: center;
    align-items: center;
}


/* */

.search-my-msg {
    width: 90%;
    margin: 0 auto 1rem;
    padding: .5rem 2.5%;
    border-radius: 0 0 .4rem .4rem;
    background: #fff;
}


/**/

.search-model {
    margin: .5rem 0;
    justify-content: space-between;
    -ms-align-items: center;
    align-items: center;
}

.search-model-title {
    color: #015480;
    margin-right: .5rem;
}

.search-model-titleFlag {
    font-size: .875rem;
}

.search-model-msg {
    -webkit-flex: 1;
    -moz-flex: 1;
    -ms-flex: 1;
    -o-flex: 1;
    flex: 1;
    background: #efefed;
    border-radius: 1rem;
    padding: 1px .75rem;
    color: #484848;
    font-size: .875rem;
}
</style>



