<template>
    <div class="address flexCol">
        <!--  -->
        <errAlert :errShow="errShow" :errTxt="errTxt"></errAlert>
        <loading v-if="loading"></loading>
        <smallUser></smallUser>
        <!--  -->
        <h1 class="validate" v-if="validate">{{validate}}</h1>
        <div class="add-item flexRow borderBox">
            <span></span>
            <select name="province" class="info" v-model="province" @change='changePro(province)'>
                <option v-for="(item,index) in addProList" v-bind:value="item.Id">{{item.Name}}</option>
            </select>
        </div>
        <div class="add-item flexRow borderBox">
            <span></span>
            <select name="city" class="info" v-model="city" @change='changeCity(city)'>
                <option v-for="(item,index) in addCityList" v-bind:value="item.Id">{{item.Name}}</option>
            </select>
        </div>
        <div class="add-item flexRow borderBox">
            <span></span>
            <select name="area" class="info" v-model="area" @change='changeArea()'>
                <option v-for="(item,index) in addAreaList" v-bind:value="item.Id">{{item.Name}}</option>
            </select>
        </div>
        <div class="add-item flexRow borderBox add-area">
            <textarea class="textarea borderBox" v-model="details" placeholder=""></textarea>
        </div>
        <button class="btn" @click="postAdd"></button>
    </div>
</template>
<script type="text/javascript">
export default {
    name: 'address',
    data() {
        return {
            loading: true,
            resData: '',
            // 
            addProList: {},
            addCityList: {},
            addAreaList: {},
            // 
            province: '',
            city: '',
            area: '',
            details: '',
            // 
            address: {
                province: '',
                city: '',
                area: '',
                address: '',
            },
            // 
            validate: '',
            // 
            addressSuccess: {
                proSu: false,
                citySu: false,
                countySu: false,
                detailsSu: false
            },
            // 
            errShow: false,
            errTxt: '',
            parentid: 100000,
        }
    },
    mounted() {
        this.$nextTick(function() {
            this.getProList();
            this.activateGetAddress();
        });
    },
    // 
    activated() {
        document.title = "";
        this.loading = true;
        // 
        this.addressSuccess.proSu = false;
        this.addressSuccess.citySu = false;
        this.addressSuccess.countySu = false;
        this.addressSuccess.detailsSu = false;
        // 
        this.activateGetAddress();
    },
    methods: {
        // 
        activateGetAddress() {
            var that = this;
            // 
            this.$tools.GetDataFromServer(
                this,
                this.API.doubleGetAdd,
                function success(res) {
                    that.loading = false;
                    var resData = res.data.Info;
                    that.province = resData.Province;
                    that.city = resData.City;
                    that.area = resData.Area;
                    that.details = resData.Address;
                },
                function error(err) {
                    that.loading = false;
                    that.errShow = true;
                    that.errTxt = ''
                    var errLoading = setTimeout(function() {
                        that.errShow = false;
                        that.errTxt = ''
                        clearTimeout(errLoading);
                    }, 2000)
                }
            )
        },
        // ->
        changePro(id) {
            this.parentid = id;
            this.addAreaList = {};
            this.details = '';
            this.getCityList();
        },
        // ->
        changeCity(id) {
            this.parentid = id;
            this.details = '';
            this.getAreaList();
        },
        changeArea() {
            this.details = '';
        },
        // 
        getProList() {
            var that = this;
            // 
            this.$tools.GetDataFromServer(
                this,
                this.API.getAdd + this.parentid,
                function success(res) {
                    that.loading = false;
                    var resData = res.data;
                    that.addProList = resData.AreaInfo.Itemlist;
                },
                function error(err) {
                    that.loading = false;
                    that.errShow = true;
                    that.errTxt = ''
                    var errLoading = setTimeout(function() {
                        that.errShow = false;
                        that.errTxt = ''
                        clearTimeout(errLoading);
                    }, 2000)
                }
            )
        },
        // 
        getCityList() {
            var that = this;
            // 
            this.$tools.GetDataFromServer(
                this,
                this.API.getAdd + this.parentid,
                function success(res) {
                    var resData = res.data;
                    that.addCityList = resData.AreaInfo.Itemlist;
                },
                function error(err) {
                    that.errShow = true;
                    that.errTxt = ''
                    var errLoading = setTimeout(function() {
                        that.errShow = false;
                        that.errTxt = ''
                        clearTimeout(errLoading);
                    }, 2000)
                }
            )
        },
        // 
        getAreaList() {
            var that = this;
            // 
            this.$tools.GetDataFromServer(
                this,
                this.API.getAdd + this.parentid,
                function success(res) {
                    var resData = res.data;
                    that.addAreaList = resData.AreaInfo.Itemlist;
                },
                function error(err) {
                    that.errShow = true;
                    that.errTxt = ''
                    var errLoading = setTimeout(function() {
                        that.errShow = false;
                        that.errTxt = ''
                        clearTimeout(errLoading);
                    }, 2000)
                }
            )
        },
        // 
        checkPro() {
            if (this.province == '') {
                this.validate = ''
            } else {
                this.address.province = this.province;
                this.addressSuccess.proSu = true;
            }
        },
        checkCity() {
            if (this.city == '') {
                this.validate = ''
            } else {
                this.address.city = this.city;
                this.addressSuccess.citySu = true;
            }
        },
        checkCounty() {
            if (this.area == '') {
                this.validate = ''
            } else {
                this.address.area = this.area;
                this.addressSuccess.countySu = true;
            }
        },
        checkDetails() {
            if (this.details == '') {
                this.validate = ''
            } else {
                this.address.address = this.details;
                this.addressSuccess.detailsSu = true;
            }
        },
        // &
        postAdd() {
            this.checkPro();
            // 
            if (this.addressSuccess.proSu) {
                this.checkCity()
                if (this.addressSuccess.citySu) {
                    this.checkCounty()
                    if (this.addressSuccess.countySu) {
                        this.checkDetails()
                        if (this.addressSuccess.detailsSu) {
                            this.validate = '';
                            var that = this;
                            this.loading = true;
                            // 
                            this.$tools.PostDataToServer(
                                this,
                                this.API.postAdd,
                                this.address,
                                function success(res) {
                                    that.loading = false;
                                    var resData = res.data;
                                    // 
                                    if (resData.State.Code == 1) {
                                        that.$router.replace('/user');
                                    }
                                },
                                // 
                                function error(err) {
                                    that.loading = false;
                                    that.errShow = true;
                                    that.errTxt = ''
                                    var errLoading = setTimeout(function() {
                                        that.errShow = false;
                                        that.errTxt = ''
                                        clearTimeout(errLoading);
                                    }, 2000)
                                }
                            )
                        }
                    }
                }
            }
        }
    }
}
</script>
<style type="text/css" scoped>
.address {
    -ms-align-items: center;
    align-items: center;
}


/**/

.add-item {
    width: 100%;
    height: 3rem;
    padding: 0 5%;
    border-bottom: 1px solid #eee;
    font-size: .875rem;
    justify-content: space-between;
    -ms-align-items: center;
    align-items: center;
}

.add-item span {
    width: 4rem;
}

.add-item select {
    height: 2rem;
    border-radius: .2rem;
    border: 1px solid #ddd;
    outline: none;
    color: #606060;
    -webkit-flex: 1;
    -moz-flex: 1;
    -ms-flex: 1;
    -o-flex: 1;
    flex: 1;
}


/**/

.add-area .textarea {
    width: 100%;
    height: 4rem;
    font-size: .875rem;
    margin: .5rem auto;
    border: 1px solid #ddd;
    outline: none;
    border-radius: .2rem;
    padding: 1% 2%;
}

.add-area {
    height: auto;
    border-bottom: none;
}

.btn {
    width: 65%;
}
</style>



