<template>
    <div class="payOtherSee flexCol">
        <!--  -->
        <errAlert :errShow="errShow" :errTxt="errTxt"></errAlert>
        <loading v-if="loading"></loading>
        <!--  -->
        <div class="mask" v-if="confirmShow"></div>
        <div class="UserUnbund_window flexCol" v-if="confirmShow">
            <h1 class="borderBox"></h1>
            <div class="flexRow">
                <button type="button" class="canBtn btn" @click="cancelUnbund"></button>
                <button type="button" class="btn" @click="successUnbund"></button>
            </div>
        </div>
        <!--  -->
        <div class="small-header">
            <p class="unbundUser" @click="unbundUser"></p>
        </div>
        <!--  -->
        <div class="OtherUserMsg flexCol">
            <div class="otherUser-model flexRow">
                <span class="userMsg-title"></span>
                <span class="userMsg-kind">{{userMoreMsg.UserName}}</span>
            </div>
            <div class="otherUser-model flexRow">
                <span class="userMsg-title"></span>
                <span class="userMsg-kind">{{userMoreMsg.RelationShip}}</span>
            </div>
            <div class="otherUser-model flexRow">
                <span class="userMsg-title"></span>
                <span class="userMsg-kind">{{userMoreMsg.IdCard}}</span>
            </div>
            <div class="otherUser-model flexRow">
                <span class="userMsg-title"></span>
                <span class="userMsg-kind">{{userMoreMsg.Phone}}</span>
            </div>
            <div class="otherUser-model flexRow" v-if="getOtherUser.IsPay == 0">
                <span class="userMsg-title"></span>
                <span class="userMsg-kind">{{userMoreMsg.type}}</span>
            </div>
            <div class="otherUser-model flexRow" v-if="getOtherUser.IsPay == 0">
                <span class="userMsg-title"></span>
                <span class="userMsg-kind">{{userMoreMsg.total_fee}}</span>
            </div>
            <div class="otherUser-model flexRow noBorder" v-if="getOtherUser.IsPay == 0">
                <span class="userMsg-title"></span>
                <span class="userMsg-kind">{{userMoreMsg.valid_period}}</span>
            </div>
            <div class="otherUser-model flexRow noBorder" v-if="getOtherUser.IsPay == 1">
                <span class="userMsg-title"></span>
                <span class="userMsg-kind">{{userMoreMsg.currentdeadline}}</span>
            </div>
            <button class="btn" @click="payOtherGo" type="button" v-if="getOtherUser.IsPay == 0"></button>
            <div class="noPay flexRow" v-if="getOtherUser.IsPay == 1">
                <span></span>
            </div>
        </div>
    </div>
</template>
<script type="text/javascript">
import loading from "@/components/loading"
export default {
    name: 'payOtherSee',
    data() {
        return {
            loading: true,
            userMoreMsg: {},
            getOtherUser: {
                IsPay: '',
                IdCard: '',
                UserName: '',
                status: 2
            },
            // 
            confirmShow: false,
            // 
            errShow: false,
            errTxt: ''
        }
    },
    activated() {
        document.title = "";
        // idid
        this.getOtherUser.IsPay = this.$route.params.msg.IsPay;
        this.getOtherUser.IdCard = this.$route.params.msg.IdCard;
        this.getOtherUser.UserName = this.$route.params.msg.UserName;
        this.getUserMoreMsg();
    },
    methods: {
        // 
        getUserMoreMsg() {
            var that = this;
            this.$tools.PostDataToServer(
                this,
                this.API.seeOther,
                this.getOtherUser,
                function success(res) {
                    var resData = res.data;
                    that.loading = false;
                    if (resData.State.Code == 1) {
                        that.userMoreMsg = resData.Info;
                    }
                },
                function error(err) {
                    that.loading = false;
                    that.errShow = true;
                    that.errTxt = ''
                    var errLoading = setTimeout(function() {
                        that.errShow = false;
                        that.errTxt = ''
                        clearTimeout(errLoading);
                    }, 2000)
                }
            )
        },
        // 
        payOtherGo() {
            var that = this;
            // 
            this.$tools.PostDataToServer(
                this,
                this.API.payOther,
                this.getOtherUser,
                function success(res) {
                    var resData = res.data;
                    that.weixinPay(resData);
                },
                function error(err) {
                    that.errShow = true;
                    that.errTxt = ''
                    var errLoading = setTimeout(function() {
                        that.errShow = false;
                        that.errTxt = ''
                        clearTimeout(errLoading);
                    }, 2000)
                }
            );
        },
        /**
         * @method :
         * @param data
         */
        weixinPay (data) {
            var that = this;
            if (typeof WeixinJSBridge == "undefined") { //
                if (document.addEventListener) {
                    document.addEventListener('WeixinJSBridgeReady', that.onBridgeReady(data), false);
                } else if (document.attachEvent) {
                    document.attachEvent('WeixinJSBridgeReady', that.onBridgeReady(data));
                    document.attachEvent('onWeixinJSBridgeReady', that.onBridgeReady(data));
                }
            } else {
                that.onBridgeReady(data);
            }
        },
        /**
         * @method 
         * @param data:,(APIH5);
         */
        onBridgeReady (data) {
            var that = this;
            WeixinJSBridge.invoke(
                'getBrandWCPayRequest', {
                    "appId": data.appId, //
                    "timeStamp": data.timeStamp, //1970
                    "nonceStr": data.nonceStr, //
                    "package": data.package,
                    "signType": data.signType, //
                    "paySign": data.paySign //
                },
                function(res) {
                    that.loading = true;
                    // ,res.err_msgok
                    if (res.err_msg == "get_brand_wcpay_request:ok") {
                        that.getUserMoreMsg();
                    } else {
                        that.loading = false;
                        alert("");
                    }
                }
            );
        },
        // 
        cancelUnbund() {
            this.confirmShow = false;
        },
        successUnbund() {
            var that = this;
            this.loading = true;
            this.$tools.PostDataToServer(
                this,
                this.API.unbundOther,
                this.getOtherUser,
                function success(res) {
                    that.loading = false;
                    var resData = res.data;
                    // 
                    if (resData.State.Code == 1) {
                        that.$router.replace('/pay-other');
                    }
                },
                // 
                function error(err) {
                    that.errShow = true;
                    that.loading = false;
                    that.errTxt = ''
                    var errLoading = setTimeout(function() {
                        that.errShow = false;
                        that.errTxt = ''
                        clearTimeout(errLoading);
                    }, 2000)
                }
            )
        },
        unbundUser() {
            this.confirmShow = true;
        }
    },
    components: {
        loading
    }
}
</script>
<style type="text/css" scoped>
.small-header {
    height: 2rem;
}


/**/

.unbundUser {
  width: 95%;
  text-align: right;
  color: #fff;
  line-height: 2rem;
  font-size: .875rem;
}


/*pay-other common*/

.payOtherSee {
    font-size: .875rem;
    justify-content: center;
    -ms-align-items: center;
    align-items: center;
}


/**/

.OtherUserMsg {
    border: 1px solid #ddd;
    font-size: .875rem;
    margin: 1rem auto .5rem;
    width: 80%;
    padding: 0 5%;
    border-radius: .5rem;
}

.otherUser-model {
    width: 100%;
    min-height: 2rem;
    -ms-align-items: center;
    align-items: center;
    padding: .25rem 0;
    border-bottom: 1px dashed #ddd;
}

.otherUser-model:last-child {
    border: none;
    margin-bottom: 0;
}

.otherUser-model .noBorder {
    border-bottom: none;
}


/**/

.userMsg-title {
    color: #31679C;
    font-weight: bold;
    width: 6rem;
}


/**/

.userMsg-kind {
    color: #5F5F5F;
    background: #EFEFEF;
    padding: 2px .5rem;
    margin: .5rem;
    border-radius: .2rem;
    font-weight: normal;
    -webkit-flex: 1;
    -moz-flex: 1;
    -ms-flex: 1;
    -o-flex: 1;
    flex: 1;
}

.btn {
    width: 100%;
}


/**/

.noPay {
    margin-bottom: 1rem;
    text-align: center;
    font-weight: bold;
    -ms-align-items: center;
    align-items: center;
}


/**/

.UserUnbund_window {
    color: #fff;
    position: fixed;
    width: 15rem;
    height: 8rem;
    background: #fff;
    margin-left: -7.5rem;
    left: 50%;
    top: 30%;
    border-radius: .5rem .5rem 0 0;
    z-index: 9999;
    -ms-align-items: center;
    align-items: center;
}

.UserUnbund_window h1 {
    width: 100%;
    height: 3rem;
    line-height: 3rem;
    padding-left: 5%;
    font-size: .875rem;
    text-align: left;
    color: #fff;
    border-radius: .5rem .5rem 0 0;
    background: #5BB2F7;
}

.UserUnbund_window div {
    width: 100%;
    height: 5rem;
    -ms-align-items: center;
    align-items: center;
    justify-content: space-around;
}

.UserUnbund_window .btn {
    width: 45%;
}

.UserUnbund_window .canBtn {
    background: #C8C8C8;
}
</style>



