<template>
    <div class="modPhone flexCol">
        <!--  -->
        <errAlert :errShow="errShow" :errTxt="errTxt"></errAlert>
        <loading v-if="loading"></loading>
        <smallUser></smallUser>
        <!--  -->
        <h1 class="validate" v-if="validate">{{validate}}</h1>
        <!--  -->
        <div class="modPhone-model flexRow borderBox">
            <span></span>
            <p>{{userMsg.username}}</p>
        </div>
        <!--  -->
        <div class="modPhone-model flexRow borderBox">
            <span></span>
            <p>{{userMsg.phone}}</p>
        </div>
        <!--  -->
        <div class="modPhone-model flexRow borderBox noBorder">
            <input type="text" maxlength="6" v-model="verification" placeholder="6"></input>
            <!--  -->
            <button type="button" @click="getCode()" :disabled="!showVer" class="verificationBtn borderBox">
                <span v-show="showVer"></span>
                <span v-show="!showVer" class="count">({{count}}s)</span>
            </button>
        </div>
        <button type="button" @click="subPhone" class="modPhoneBtn"></button>
        <p class="sign">
            <router-link to="/appealPhone" class="appealBtn"></router-link>
        </p>
    </div>
</template>
<script type="text/javascript">
export default {
    name: 'modPhone',
    data() {
        return {
            loading: false,
            userMsg: {},
            validate: '',
            // 
            showVer: true,
            count: '',
            timer: null,
            TIME_COUNT: 60,
            // 
            verification: '',
            // 
            userPhoneMsg: {
                phone: '',
                verification: ''
            },
            // 
            errShow: false,
            errTxt: '',
        }
    },
    activated() {
        document.title = "";
        this.validate = '';
        this.userPhoneMsg = {};
        this.verification = '';
        this.userMsg = this.$route.params.userMsg;
    },
    methods: {
        // 
        getCode(phone) {
            // 
            this.userPhoneMsg.phone = this.userMsg.phones;
            // 
            if (!this.timer) {
                // 
                this.count = this.TIME_COUNT;
                this.showVer = false;
                // 
                this.timer = setInterval(() => {
                        if (this.count > 1 && this.count <= this.TIME_COUNT) {
                            this.count--;
                        } else {
                            this.showVer = true;
                            clearInterval(this.timer);
                            this.timer = null;
                        }
                    }, 1000)
                    // 
                this.$tools.PostDataToServer(
                    this,
                    this.API.getCode,
                    this.userPhoneMsg,
                    function success(res) {
                        console.log(res);
                    },
                    function error(err) {
                        that.errShow = true;
                        that.errTxt = ''
                        var errLoading = setTimeout(function() {
                            that.errShow = false;
                            that.errTxt = ''
                            clearTimeout(errLoading);
                        }, 2000)
                    }
                )
            }
        },
        // 
        subPhone() {
            var verification = this.verification,
                verificationRe = /^\d{6}$/,
                that = this;
            // 
            this.userPhoneMsg.verification = this.verification;
            // 
            if (!verificationRe.test(verification)) {
                this.validate = ""
            } else {
                this.validate = '';
                this.loading = true;
                // 
                this.$tools.PostDataToServer(
                    this,
                    this.API.modPhone,
                    this.userPhoneMsg,
                    function success(res) {
                        that.loading = false;
                        var resData = res.data;
                        // 
                        if (resData.State.Code == 1) {
                            that.$router.replace('/modNewPhone');
                        }
                        // 
                        if (resData.State.Code == 0) {}
                    },
                    // 
                    function error(err) {
                        that.loading = false;
                        that.errShow = true;
                        that.errTxt = ''
                        var errLoading = setTimeout(function() {
                            that.errShow = false;
                            that.errTxt = ''
                            clearTimeout(errLoading);
                        }, 2000)
                    }
                )
            }
        }
    }
}
</script>
<style type="text/css" scoped>
.modPhone {
    -ms-align-items: center;
    align-items: center;
}


/**/

.modPhone-model {
    width: 100%;
    padding: 0 5%;
    height: 4rem;
    font-size: .875rem;
    background: #fff;
    border-bottom: 1px solid #ddd;
    margin: 0 auto;
    justify-content: space-between;
    -ms-align-items: center;
    align-items: center;
}

.modPhone-model>span {
    color: #5f5f5f;
    background: #EFEFEF;
    padding: .2rem .75rem;
    border-radius: .2rem;
    margin-right: 1rem;
}

.modPhone-model input {
    outline: none;
    border-radius: .2rem;
    border: 1px solid #ddd;
    height: 2rem;
}


/**/

.modPhone .verificationBtn {
    min-width: 8rem;
    padding: .5rem;
    font-size: .875rem;
    outline: none;
    border: none;
    border-radius: .2rem;
    background: #7DC5DC;
    text-align: center;
    color: #fff;
}

.modPhoneBtn {
    background: #3591cf;
    border: none;
    border-radius: .2rem;
    color: #fff;
    width: 90%;
    height: 2.5rem;
    margin: 2rem auto 0;
    outline: none;
}


/**/

.sign {
    width: 90%;
    text-align: right;
}

.appealBtn {
    font-weight: bold;
    margin-left: .3rem;
    color: #E50000;
}
</style>



