<template>
    <div class="index">
        <!--  -->
        <keep-alive>
            <user @flag="flagPop" @type="typePop"></user>
        </keep-alive>
        <router-view></router-view>
        <!--  -->
        <div v-if="tabMana">
            <span class="medical-title"></span>
            <ul class="medical-manage flexRow">
                <router-link tag="li" class="flexCol" :to="{path: list_1}">
                    <img src="../../assets/index/mid-search.png">
                    <span></span>
                </router-link>
                <router-link tag="li" class="flexCol" :to="{path: list_2}" v-if="userType == 2">
                    <img src="../../assets/index/mid-pay.png">
                    <span></span>
                </router-link>
                <router-link tag="li" class="flexCol" :to="{path: list_3}">
                    <img src="../../assets/index/mid-notice.png">
                    <span></span>
                </router-link>
            </ul>
            <!--  -->
            <otherMsg></otherMsg>
        </div>
    </div>
</template>
<script>
import user from "@/components/user.vue"
import otherMsg from "@/components/otherMsg.vue"
export default {
    name: 'index',
    data() {
        return {
            userFlag: '',
            userType: '',
            list_1: "search",
            list_2: "pay-my",
            list_3: "pay-other",
            tabMana: true
        }
    },
    // 
    beforeMount() {
        if (this.$store.state.userType == '') {
            this.userType = 1;
        }
        if (this.$store.state.userType == '' || this.$store.state.userType == 1) {
            this.userType = 2;
        }
    },
    mounted() {
        //
        this.flagPop();
    },
    activated() {
        document.title = "";
    },
    watch: {
        "$route": "fetchDate"
    },
    methods: {
        // data->, 
        flagPop(data) {
            this.$store.state.userFlag = data;
            this.userFlag = data;
            // true
            if (this.userFlag == true) {
                this.list_1 = 'register';
                this.list_2 = 'register';
                this.list_3 = 'register';
            }
            // 
            if (this.userFlag == false) {
                // 
                if (this.userType == 1) {
                    this.list_1 = "search-my"
                } else {
                    this.list_1 = 'search';
                }
                this.list_2 = 'pay-my';
                this.list_3 = 'pay-other';
            }
        },
        // otherMsg
        fetchDate() {
            if (this.$route.name == 'search') {
                this.tabMana = false;
            }
            if (this.$route.name == 'Index') {
                this.tabMana = true;
            }
        }
    },
    components: {
        user,
        otherMsg
    }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.index {
    width: 100%;
}

.medical-manage {
    width: 100%;
    height: 6.5rem;
    border-bottom: .5rem solid #EDF2F5;
    justify-content: space-around;
}

.medical-manage li {
    width: 33.3%;
    justify-content: center;
    -ms-align-items: center;
    align-items: center;
}

.medical-manage li img {
    height: 2.5rem;
    margin-bottom: .5rem;
}

.medical-manage li span {
    font-size: .875rem;
    font-weight: bold;
    color: #31679C;
}

.medical-title {
    display: block;
    margin: 1rem 5% 0;
    font-size: .875rem;
    color: #8C8C8C;
}
</style>



