<template>
    <div id="app">
        <!-- loading -->
        <div id="loading" v-if="loading">
            <div class="loadingBottom">
                <h1></h1>
                <img class="loadingImg" src="./assets/common/Heart.gif">
                <p>...</p>
            </div>
        </div>
        <!-- loading,v-show-->
        <div v-show="!loading">
            <!-- & -->
            <keep-alive>
                <router-view></router-view>
            </keep-alive>
            <!-- tabBar  -->
            <div class="tabHack"></div>
            <ul class="tabBarContainer">
                <!-- router list-for -->
                <router-link v-for="(item,index) in tabBarList" @click.native="touchBar(index)" tag="li" class="tabBar" :to="{path: item.path}" replace>
                    <!-- tabBar icon & msg -->
                    <img v-if="index === isIndex" :src="item.imgUrlChoose">
                    <img v-else :src="item.imgUrl">
                    <span>{{item.msg}}</span>
                </router-link>
            </ul>
        </div>
    </div>
</template>
<script>
// 
import './css/reset.css'
import './css/media.css'
import './css/common.css'
export default {
    name: 'app',
    data() {
        return {
            tabBarList: [{
                path: "/",
                imgUrl: require('./assets/tabBar/index.png'),
                imgUrlChoose: require('./assets/tabBar/indexChoose.png'),
                msg: ""
            }, {
                path: "notice",
                imgUrl: require('./assets/tabBar/about.png'),
                imgUrlChoose: require('./assets/tabBar/aboutChoose.png'),
                msg: ""
            }, {
                path: "register",
                imgUrl: require('./assets/tabBar/center.png'),
                imgUrlChoose: require('./assets/tabBar/centerChoose.png'),
                msg: ""
            }],
            isIndex: 0,
            // 
            loading: true
        }
    },
    beforeCreate() {
        // ajax
        var that = this;
        var xhr = new XMLHttpRequest();
        xhr.open("get",'Check/IsSession',false);
        xhr.onreadystatechange = function() {
          if (xhr.readyState == 4 && xhr.status == 200) {
            // 1
            if (xhr.responseText == 1 || xhr.responseText == "" || xhr.responseText == "") {
              that.$store.state.userType = xhr.responseText;
            }
            // 1Home
            if (xhr.responseText == 0) {
              window.location.href = process.env.API_HOST + '/home';
            }
          }
        };
        // ajax
        xhr.send();
    },
    created(){
      this.loadingGo();
    },
    // 
    watch: {
        "$route": "fetchDate"
    },
    // tabBar
    methods: {
        // tabBar
        touchBar(index) {
            this.isIndex = index;
        },
        // 
        loadingGo() {
            var that = this;
            // 
            var loadingTimer = setInterval(function() {
                // 
                var flag = that.$store.state.userFlag
                    // undefined
                if (flag != undefined) {
                    // loadingloadingHide
                    that.loadingHide();
                    clearInterval(loadingTimer);
                }
                // 2s
            }, 2000);
        },
        // 
        loadingHide() {
            this.loading = false;
            if (this.$store.state.userFlag == true) {
                this.tabBarList[2].path = 'register';
            }
            if (this.$store.state.userFlag == false) {
                this.tabBarList[2].path = 'center';
            }
        },
        // tabbug
        fetchDate() {
            if (this.$route.name == 'Index') {
                this.isIndex = 0;
            }
        }
    }
}
</script>
<style>
body,
#app,
#app>div {
    height: 100%;
}
/*hack*/

.msgContent img {
    width: 100%;
    margin: .5rem 0;
    border-radius: .2rem;
    text-indent: 0rem !important;
}

.o-detail .msg,
.n-detail .msg {
    font-size: .75rem !important;
    margin: 1rem 0;
    text-align: left;
    color: #767676;
    display: inline-block;
    background: #eee;
    border-radius: .5rem;
    padding: .1rem .5rem;
}

/*tabBar common*/

.tabBarContainer {
    width: 100%;
    height: 4.375rem;
    position: fixed;
    bottom: 0;
    z-index: 10;
    border-top: 1px solid #ddd;
    background: #fff;
    display: box;
    display: -webkit-box;
    display: -moz-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
}

.tabBar {
    width: 33.3%;
    height: 100%;
    display: box;
    display: -webkit-box;
    display: -moz-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-flex-direction: column;
    -moz-flex-direction: column;
    -ms-flex-direction: column;
    -o-flex-direction: column;
    flex-direction: column;
    justify-content: center;
    -ms-align-items: center;
    align-items: center;
}

.tabBar img {
    width: 2rem;
    height: 2rem;
    margin-bottom: .1rem;
}

.tabBar span {
    font-size: .875rem;
    color: #31679C;
}

.tabHack {
    width: 100%;
    height: 4.3rem;
}

#loading {
    position: fixed;
    width: 100%;
    height: 100%;
    background: url(./assets/index/loadingBg.jpg);
    -webkit-background-size: 100%;
    background-size: 100%;
    top: 0;
    z-index: 9999;
    /**/
    -webkit-animation-name: 'fade-in';
    -o-animation-name: 'fade-in';
    animation-name: 'fade-in';
    -webkit-animation-duration: 2s;
    -o-animation-duration: 2s;
    animation-duration: 2s;
    /*flex*/
    display: box;
    display: -webkit-box;
    display: -moz-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-flex-direction: column;
    -moz-flex-direction: column;
    -ms-flex-direction: column;
    -o-flex-direction: column;
    flex-direction: column;
    justify-content: flex-end;
    -ms-align-items: center;
    align-items: center;
}

.loadingBottom {
    color: #fff;
    font-size: .75rem;
    /*flex*/
    display: box;
    display: -webkit-box;
    display: -moz-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-flex-direction: column;
    -moz-flex-direction: column;
    -ms-flex-direction: column;
    -o-flex-direction: column;
    flex-direction: column;
    -ms-align-items: center;
    align-items: center;
}

.loadingImg {
    width: 3.5rem;
}

.loadingBottom h1 {
    color: #fff;
    font-size: 2rem;
    margin: 0 auto 1rem;
}

.loadingBottom p {
    margin-bottom: 3rem;
}


/*loading*/

@keyframes fade-in {
    0% {
        -webkit-filter: sepia(100%);
        -o-filter: sepia(100%);
        filter: sepia(100%);
    }
    50% {
        -webkit-filter: sepia(50%);
        -o-filter: sepia(50%);
        filter: sepia(50%);
    }
    100% {
        -webkit-filter: sepia(0%);
        -o-filter: sepia(0%);
        filter: sepia(0%);
    }
}
</style>



